.landing-footer {
	display: flex;
	justify-content: center;
	align-items: center;

	width: 100%;
	height: 120px;

	background-color: var(--light-bg);
}

.footer-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
}
.footer-link {
	color: var(--Black-75, rgba(0, 0, 0, 0.5));
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: 209%;

	cursor: none;
}
